import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { IonicStorageModule, Storage } from '@ionic/storage';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MyApp } from './app.component';

import { ProfilePage, ProfileCommentsPage, ContactPage, GamePage, 
  GameLoaderPage, QuestionsPage, ReplyLoaderPage, 
  ReplyPage, SettingsPage, SettingsInterestsPage } from '../pages/pages';
import { Facebook } from "@ionic-native/facebook";
import { CategoryProvider, FacebookService } from '../providers/providers';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { NativePageTransitions, NativeTransitionOptions } from '@ionic-native/native-page-transitions';

@NgModule({
  declarations: [
    MyApp,
    ProfilePage,
    ProfileCommentsPage,
    ContactPage,
    GamePage, 
    GameLoaderPage,
    ReplyPage, 
    ReplyLoaderPage,
    QuestionsPage,
    SettingsPage, 
    SettingsInterestsPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {
      backButtonText: '',
      mode: 'ios'
    }),
    IonicStorageModule.forRoot({
      name: '__quizzydb'
         //driverOrder: ['localstorage' ]
    }),
    BrowserAnimationsModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    ProfilePage,
    ProfileCommentsPage,
    ContactPage,
    GamePage, 
    GameLoaderPage,
    ReplyPage, 
    ReplyLoaderPage,
    QuestionsPage,
    SettingsPage,
    SettingsInterestsPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    NativePageTransitions,
    CategoryProvider, 
    Facebook,
    FacebookService
  ]
})
export class AppModule {}



