import { Component } from '@angular/core';
import {DomSanitizer} from "@angular/platform-browser";
import { ViewController, AlertController, NavController, ToastController, ModalController, NavParams } from 'ionic-angular';
import { Storage } from "@ionic/storage";

import { AbstractPage } from '../abstract';
import { QuestionsPage } from '../pages';

@Component({
	selector: 'page-reply',
	templateUrl: 'reply.html'
})
export class ReplyPage extends AbstractPage {
	temps:number = 10;
	taux:number;
	remaining:string = "10";
	questions:any[];
	currentQuestionId:number = 0;
	currentQuestion:any;

	currentResponse:any;
	replied:boolean = false;

	constructor(public viewCtrl: ViewController,
		public navCtrl: NavController,
		public alertCtrl: AlertController,
		public storage: Storage,
        public toastCtrl: ToastController,
        public modalCtrl: ModalController,
        public params: NavParams) {
		super(viewCtrl, navCtrl, alertCtrl, toastCtrl, modalCtrl, params);
		this.mock();
		this.currentQuestion = this.questions[this.currentQuestionId];
	}

	setGood(answerChosen){
		if(this.questions[this.currentQuestionId].goodAnswer == answerChosen){
			this.replied = false;
			this.questions[this.currentQuestionId].goodAnswer = null;
		}else{
			this.replied = true;
			this.questions[this.currentQuestionId].goodAnswer = answerChosen;
		}
	}

	getClass(answerChosen){
		if(answerChosen == this.currentQuestion.goodAnswer){
			return 'good';
		}
		return '';

	}

	nextQuestion(){
		this.currentQuestionId++;
		this.currentResponse =null;
		if(this.currentQuestionId == this.questions.length){
			console.log("STOP");
		}else{
			this.currentQuestion = this.questions[this.currentQuestionId];
		}
	}

	quit(){
		this.goTo(QuestionsPage, 'backward', {});
	}

	mock(){
		this.questions = [{
			'title':"En quelle année je suis né ?",
			'category': {
				'title': 'Gastronomie',
				'image': 'http://www.leshallesdenimes.com/imgs/images/etal-bio/Fruits-Legumes.jpg'
			},
			'answers':[{
				"id": 1,
				'text': "1986",

			},{
				"id": 2,
				'text': "1987"
			},{
				"id": 3,
				'text': "1988"
			},{
				"id": 4,
				'text': "1989"
			},{
				"id": 5,
				'text': "1990"
			},{
				"id": 6,
				'text': "1991"
			},{
				"id": 7,
				'text': "1992"
			},{
				"id": 8,
				'text': "1993"
			}
			],
			'goodAnswer':null
		},
		{
			'title':"Quel est mon plat préféré ?",
			'category': {
				'title': 'Culture générale',
				'image': 'https://pbs.twimg.com/profile_images/2587029738/jl43207kyoox2f9gorbo_400x400.png'
			},
			'answers':[{
				"id": 5,
				'text': "Le tajine",

			},{
				"id": 6,
				'text': "Le bon vieux kebab"
			},{
				"id": 7,
				'text': "La blanquette de veau"
			},{
				"id": 8,
				'text': "Le poulet chasseur"
			},{
				"id": 9,
				'text': "Les raviolis"
			},{
				"id": 10,
				'text': "Le poulet basquaise"
			},{
				"id": 11,
				'text': "La salade nicoise"
			},{
				"id": 12,
				'text': "Le pot-au-feu"
			}
			],
			'goodAnswer':null
		}
		]
	}

}



